/* Import Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Righteous&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato&display=swap');

/* Reset */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Global Styles */
body {
  font-family: "Lato", sans-serif;
  background-color: #121212;
  color: #eaeaea;
  overflow-x: hidden;
}

/* Main Container */
.container {
  max-width: 1400px; /* Set maximum width of the page */
  margin: 0 auto; /* Center the container */
  padding: 0 20px; /* Add horizontal padding */
}


/* Experience Header */
.experience-header {
  text-align: center;
  margin-bottom: 50px;
}

.experience-title {
  font-family: "Righteous", sans-serif;
  font-size: 3rem;
  color: #EF0000;
  margin-bottom: 20px;
  font-weight: bold;
  text-transform: uppercase;
}

.experience-intro {
  font-family: "Lato", sans-serif;
  font-size: 1.2rem;
  color: #fff;
  max-width: 800px;
  margin: 0 auto;
  font-style: italic;
}

/* Experience Timeline */
.experience-timeline {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  z-index: 2;
}

.timeline-item {
  margin-bottom: 40px;
  padding: 30px;
  background-color: #232323;
  border-radius: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s;
  border-left: 5px solid #EF0000;
  cursor: pointer; /* Makes the timeline items look clickable */
}

.timeline-item:hover {
  transform: scale(1.02);
}

.timeline-role {
  font-family: "Righteous", sans-serif;
  font-size: 2.5rem;
  color: #1e90ff;
  margin-bottom: 10px;
  font-weight: bold;
}

.timeline-company {
  font-family: "Lato", sans-serif;
  font-size: 1.2rem;
  color: #666;
  margin-bottom: 20px;
}

.timeline-description {
  font-family: "Lato", sans-serif;
  font-size: 1.1rem;
  color: #eaeaea;
  margin-bottom: 20px;
}

.timeline-achievements {
  list-style-type: disc;
  margin-left: 20px;
}

.timeline-achievements li {
  margin-bottom: 10px;
  color: #eaeaea;
}

.timeline-tech {
  font-family: "Lato", sans-serif;
  font-style: italic;
  font-size: 1.1rem;
  color: #EF0000;
  margin-top: 15px;
}

/* Footer */
.footer {
  background-color: #0d0d0d;
  color: #eaeaea;
  padding: 40px 20px;
  text-align: center;
  border-top: 2px solid #ef0000;
}

.footer p {
  font-family: "Arial", sans-serif;
  font-size: 0.9rem;
  color: #b0b0b0;
  margin: 0;
  padding-bottom: 15px;
  letter-spacing: 0.5px;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 25px;
  margin-top: 10px;
}

.social-icon {
  color: #4FACFE;
  font-size: 1.6rem;
  text-decoration: none;
  font-weight: bold;
  border-radius: 50%;
  padding: 10px;
  transition: color 0.3s ease, background-color 0.3s ease, transform 0.3s ease;
}

.social-icon:hover {
  color: #ffffff;
  background-color: #4FACFE;
  transform: scale(1.1);
}
